@import url('https://fonts.googleapis.com/css2?family=Playfair+Display:ital,wght@0,400..900;1,400..900&family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');


*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

body{
    font-family: "Poppins", sans-serif;
    background-color: #161616;
    color: #6a6a6a;
}

/*____________________ Global _____________________*/
button{
    background-color: none;
    border: none;
    outline: none;
    word-spacing: 3px;
    letter-spacing: 1px;
    font-weight: 500;
}
p{
    font-size: 17px;
    line-height: 25px;
}
.container{
    max-width: 90%;
    margin: 0 auto;
}
.flexSB{
    display: flex;
    justify-content: space-between;
    align-items: center;
}
/*____________________ Header _____________________*/
header{
    height: 8v;
    line-height: 8vh;
}
.logo h1{
    font-size: 45px;
}
.logo h1 span{
    color: #1ebd65;
}
.toggle{
    font-size: 25px;
    color: #1ebd65;
    cursor: pointer;
    background: none;
}
.nav-links{
    display: none;
}
.nav-links-sidebar{
    position: absolute;
    display: block;
    background-color: #1ebd65;
    left: 0;
    top: 8vh;
    height: 92vh;
    width: 100%;
    z-index: 5;
    padding-left: 20px;
}
.nav-links-sidebar li ,.nav-links-sidebar li a{
    transition: all 0.3s ;

}
.nav-links-sidebar li:hover {
    color: white;
    border-bottom: 1px solid #6a6a6a ;
}
.nav-links-sidebar li:hover a{
    color: white;
}
/*____________________ Home _____________________*/
.home{
    position: relative;
}
.home .itemContent{
   padding: 20px;
   margin: 0;
   border: 20px solid #2f2f2f;
   position: relative;
   height: 88vh;

}
.home .left{
    width: 30%;
}
.home .right{
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    right: 0;
    z-index: -1;
}
.home .right img{
    width: 100%;
    height: 100%;
}
.slick-dots li.slick-active button:before {
    opacity: .75;
    color: #1ebd65 !important;
}
.slick-dots li button:before{
    font-size: 10px !important;
}
.home .left h1{
    font-size: 50px;
    word-wrap: break-word;
    margin: 30px 0;
    letter-spacing: 2;
    line-height: 60px;
}
.home .left p{
    font-size: 16px;
    line-height: 1.8;
}
.home .left img{
    margin-top: 40px;
}

/*____________________ About _____________________*/
.about{
    position: relative;
}
 .itemContent{
    
    margin:40px 0;
    border: 20px solid #2f2f2f;
    position: relative;
    height: 88vh;
    display: flex;
    overflow: hidden;
 }
 .flex{
    display: flex;
 }
 .sideHeading{
    height: 85vh;
    background-color: #222222;
    width: 170px;
    display: flex;
    justify-content: center;
    align-items: center;
 }
 .sideHeading h1{
    position: absolute;
    top: 42%;
    left: -65px;
    margin: auto;
    font-size: 38px;
    transform: rotate(-90deg);
    font-weight: 500;
 }
 .sideHeading h1 i{
    color: #1ebd65;
    margin-right: 20px;
 }
 .contentRight,
 .contentLeft{
    width: 39vw;
    padding: 0 50px; 
    position: relative;
 }
.about h2{
    font-weight: 400;
    font-size: 20px;
}
.about .h2Top{
    width: 170px;
}
.about .details{
    margin-top: 20px;
}
.primary-btn{
    padding: 7px 30px;
    background-color: #1ebd65;
    color: white;
    margin-top: 30px;
    cursor: pointer;
    border-radius: 3px;
}
.about .contentLeft::after{
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    background-image: url('../public/images/about-me-bg.jpg');
    background-size: cover;
    width: 100%;
    height: 100%;
    z-index: -1;
}
.about .heading h1{
    font-size: 50px;
    text-align: center;
    padding: 40px 0  ;
    word-spacing: 5px;
}

.about .contentRight{
    padding: 0;

}
.about .para{
    background-color: #2f2f2f;
    padding: 30px;
}
.about .para h3{
    margin: 30px 0;
    font-size: 25px;
    font-weight: 400;
}
/*____________________ Creative _____________________*/
.creative{
    position: relative;
}
.creative .contentRight{
    padding:0 ;
}
.creative img{
    width: 100%;
    height: 100%;
    object-fit: cover;
}
.creative .qoute{
    font-size: 50px;
    color: #1ebd65;
    margin: 20px 0;
}
.creative .content h1{
    font-size: 30px;
    font-weight: 500;
}
.creative .content h3{
    margin: 40px 0 30px 0;
    font-weight: 400;
    font-size: 20px;
    color: #1ebd65;
}

/*____________________ educations _____________________*/
.educations .box{
    margin-top: 20px;
}
.educations .content h1{
    font-size: 40px;
    color: #1ebd65;
    margin-right: 30px;
}
.educations .content h2{
    font-size: 18px;
    font-weight: 500;
}
.educations .contentLeft{
    padding: 20px 50px;
}
