/*____________________ Work _____________________*/
.work .sideHeading h1{
    left: -50px;
}

.work button{
    width: 250px;
    margin: 0;
    height: 30px;
    padding: 0;
}
.work.educations h2{
    margin-left: 20px;
    font-weight: 600;
    font-size: 20px;
}
.work h2 p{
    font-weight: 400;
    font-size: 15px;
}
/*____________________ Portfolio _____________________*/
.portfolio {
    position: relative;
}
.portfolio .sideHeading h1{
    left: -70px;
}

.portfolio .flex{
    display: flex !important;
}
.portfolio h1{
    font-size: 50px;
    margin: 40px 0;
}
.portfolio p{
    margin-bottom: 30px;
}
.portfolio li{
    margin: 5px 0;
}
.portfolio li i{
    color: #1ebd65;
    margin-right: 5px;
}
/*____________________ Award _____________________*/
.award .sideHeading h1{
    left: -40px;
}
.award .content h1{
    font-style: 50px;
    line-height: 50px;
    margin: 50px 0;
}
.award .box{
    display: grid;
    margin-top: 50px;
    grid-template-columns: repeat(2,1fr);
    grid-gap: 80px;
}
.award .box img{
    width: auto;
    height: auto;
    object-fit: contain;
}
/*____________________ Blog _____________________*/
.blog{
    position: relative;
}
.blog .sideHeading h1{
    left: 0;
}
.blog .box {
    display: grid;
    grid-template-columns: repeat(3,1fr);
    grid-gap: 30px;
    width: 80%;
    margin: auto;
}
.blog .box img{
    width: 100%;
    height: 100%;
    object-fit: cover;
}
/*____________________ Contact _____________________*/
.contact .content i{
    width: 40px;
   
   font-size: 20px;
   color: #1ebd65;
}
.contact .primary-btn{
    margin-top: 0;
}

.contact .contentLeft{
    padding: 20px;
}

.contact form{
    margin-top: 15px;
}
.contact form textarea,
.contact form input{
    width: 100%;
    padding: 15px;
    background: #2b2b2b;
    outline: none;
    margin-bottom: 5px;
    border: none;
    font-size:14px;
    color: white;
}